/* Reset some default styles */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Body styles */
body {
  font-family: 'Arial', sans-serif;
  background-color: #f4f4f4;
}

/* Container styles */
.container {
  max-width: 600px;
  margin: 100px auto;
  padding: 40px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.1);
}

/* Heading styles */
.heading {
  color: #333;
  margin-bottom: 20px;
  text-align: center;
}

/* Upload container styles */
.upload-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

/* Upload button styles */
.upload-btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.upload-btn:hover {
  background-color: #0056b3;
}

/* Error message styles */
.error {
  color: #ff0000;
  text-align: center;
  margin-bottom: 20px;
}

/* Prediction styles */
.prediction {
  margin-top: 20px;
  text-align: center;
}
